import { createRouter, createWebHistory } from 'vue-router'
import IntroScreen from '../components/onboarding/IntroScreen.vue'
import WelcomeScreen from '../components/welcomeScreen/WelcomeScreen.vue'
import LoginForm from '../components/auth/LoginForm.vue'
import RegistrationForm from '../components/registration/RegistrationForm.vue'
import BarberShopDetail from '../components/details/BarberShopDetail.vue'


const routes = [
  {
    path: '/',
    name: 'home',
    component: IntroScreen,
    meta: { 
      transition: 'page-opacity',

    },
    
  },
  {
    path: '/onboarding',
    name: 'onboard',
    component: WelcomeScreen,
    meta: { 
      transition: 'page-opacity',
    },
  },
  {
    path: '/login',
    name: 'login',
    component: LoginForm,
    meta: { 
      transition: 'page-opacity',
    },
  },{
    path: '/register',
    name: 'full-registration',
    component: RegistrationForm,
    meta: { 
      transition: 'page-opacity',
    },
  },{
    path: '/home',
    name: 'barber-home',
    component: BarberShopDetail,
    meta: { 
      transition: 'page-opacity',
    },
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
