import axios from 'axios'
import { backend_config } from '../../config/config'

const host = backend_config.host

export function isAuthourized() {
    return axios.post(`${host}/api/auth/check`, {
        "username" : "waxes27",
        "password" : "waxes27"
    })  
}

export function register(registerDto) {
    return axios.post(`${host}/api/auth/register`, registerDto)
}

export function login(loginDto) {
    return axios.post(`${host}/api/auth/login`, loginDto)
}

export function getBarberDetails() {
    const token = localStorage.getItem("token")
    return axios.get(`${host}/api/auth/check`, { headers: {"Authorization" : `Bearer ${token}`} })  
}